/*      +      ( /) */
const serverDomain = 'test.sneg.tv';

const appConfig = () => {
	const serverAddresses = {
		DEV: `http://${serverDomain}`,
		STAGE: `http://${serverDomain}`,
		PROD: `http://${serverDomain}`,
	};

	simplexInit();

	let serverCode = 'DEV';
	let showServer = false;
	if (Simplex.User && Simplex.User.config) {
		if (Simplex.User.config.showServer) {
			showServer = true;
		}
		if (Simplex.User.config.serverCode === 'STAGE') {
			serverCode = 'STAGE';
		} else if (Simplex.User.config.serverCode === 'PROD') {
			serverCode = 'PROD';
		} else if (Simplex.User.config.serverCode === 'AWSDEV') {
			serverCode = 'AWSDEV';
		}
	}

	const googleMapIDClear = ''; // google maps ID
	const mapLat = 55.754941;
	const mapLng = 37.617188;

	const returnVal = {
		disableFetchEmulator: true,
		serverDomain,
		apiName: 'axiosAPI',
		serverAddress: serverAddresses[serverCode],
		serverCode,
		showServer,
		basicAuthToken: 'Basic ',
		authType: 'Bearer ',
		phoneCountryCode: '7',
		printCity: false,
		fieldsLengths: { name: 3 },
		workHours: 60,
		showCityInAddress: false,
		searchItemMaxLen: 20,
		notFound: 'NOT_FOUND',
		noData: 'NO_DATA',
		defaultOnPage: 10,
		defaultSortOrder: 'DESC',
		defaultMapZoom: 8,
		defaultMapZoomBig: 10,
		defaultMapMarkerScale: 2.6,
		defaultMapLat: mapLat,
		defaultMapLng: mapLng,
		defaultMapCenter: {
			lat: mapLat,
			lng: mapLng,
		},
		inputHeight: '24px',
		dtLocation: 'ru',
		timeZone: 'Europe/Moscow',
		showSpinnerOverSkeleton: false,
		devEnvironment: process.env.NODE_ENV === 'development',
		coockieDomain: `.${serverDomain}`,
		dataReloadTime: 30000,
		MSG,
		googleMapIDClear,
		googleMapID: `https://maps.googleapis.com/maps/api/js?key=${googleMapIDClear}c&libraries=places`,
		dateWords: {
			TODAY: '',
			FOR_TODAY: ' ',
			TOMORROW: '',
			FOR_TOMORROW: ' ',
			AFTERTOMORROW: '',
			FOR_AFTERTOMORROW: '',
			YESTERDAY: '',
			FOR_YESTERDAY: ' ',
			BEFOREYESTERDAY: '',
			FOR_BEFOREYESTERDAY: ' ',
		},
	};

	return returnVal;
};

export default appConfig();
